<template>
  <view class="list">
    <view class="box" v-for="(item, index) in hootList" :key="index" @click="gotoDetail(item)">
      <view class="title">
        {{item.strategyTitle}}
      </view>
      <view class="desc">
        {{item.desc}}
      </view>
      <view class="time">
        <view>{{item.likeNum}} 赞同</view>
        <view class="dian"></view>
        <view>{{item.comNum}} 评论</view>
        <view class="dian"></view>
        <view>{{item.createTime}}</view>

      </view>
    </view>
    <view v-if="!hasMore" class="end-text">
      已经到底啦~
    </view>
  </view>
</template>

<script>
  import {
    getStrategyList
  } from '@/api/report'
  import {
    timeago
  } from '@/utils/formatTime'
  export default {
    data() {
      return {
        hootList: [],
        page: 1,
        hasMore: true,
      }
    },
    onShow() {
      this.$modal.loading("加载中，请稍后...")
      this.page = 1
      this.hootList = []
      this.getHotList();
    },
    onReachBottom() {
      this.getHotList();
    },
    methods: {
      timeAgo(val) {
        return timeago(val)
      },
      // 跳转详情页面
      gotoDetail(item) {
        console.log(item, "item");
        uni.navigateTo({
          url: "./detail?id=" + item.id
        })
      },
      // 获取热门攻略列表
      getHotList() {
        getStrategyList({
          page: this.page,
          limit: 6
        }).then(res => {
          console.log(res);
          if (res.code == 200) {
            this.hootList = this.hootList.concat(res.data.list)
            console.log(this.hootList, "hootList");
            this.page = this.page + 1
            if (res.data.hasNextPage) {
              this.hasMore = true
            } else {
              this.hasMore = false
            }
            this.hootList.forEach((item) => {
              // if (item.createTime) {
              //   item.createTime = this.timeAgo(item.createTime)
              // }
            })
          }
          this.$modal.closeLoading()
        })
      },
    }
  }
</script>

<style>
  page {
    background-color: #f6f6f6;
  }

  .box {
    background-color: #fff;
    box-sizing: border-box;
    padding: 30rpx;
    margin-bottom: 20rpx;
  }

  .title {
    font-size: 32rpx;
    color: #343434;
    font-weight: bold;
  }

  .desc {
    font-size: 26rpx;
    color: #515151;
    /* 超出2行 省略号 */
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    line-height: 1.5;
    margin: 14rpx 0;
  }

  .time {
    color: #999999;
    font-size: 22rpx;
    display: flex;
    align-items: center;
  }

  .dian {
    width: 6rpx;
    height: 6rpx;
    border-radius: 100rpx;
    background-color: #999999;
    margin: 0 10rpx;
  }

  .end-text {
    text-align: center;
    padding: 20rpx;
    color: #999;
  }
</style>